import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable()
export class GravityService {

  private appData:any = [];
  private activeApp:any = {};
  
  constructor(private http: HttpClient) { 
   this.appData= [
      {id:1, title:"One Education", description:"Nice application", link:"https://www.capita-one.co.uk/product-and-services/one-education-and-childrens-services/education-services", image:1, isPublish:true}
       ]
  }

  addApp(data){
    data["id"]=this.appData[this.appData.length-1].id+1;
    data["isPublish"]=false;
    data["image"]=this.appData[this.appData.length-1].id+1;
    this.appData.push(data);
    console.log(data);

    // this.http.post("/addapp", data).subscribe(data => {
    //   console.log(data);
    // });
  }

  getApp(){
    return this.appData;
  }

  unpublishApp(data){
    let that = this;
    data.isPublish = false;
    this.activeApp = data;
    that.http.get("/update?name=").subscribe(data => {
      console.log(data);
      this.appData.forEach(function(obj){  
        obj.isPublish = false;
      });
    });
  }

  trackApp(){
    let that = this;
    return that.http.get("/track");
  }

  publishApp(data, bool){
    let that = this;
    data.isPublish = bool;
    this.activeApp = data;
    this.appData.forEach(function(obj){      
      if(obj.id == data.id){
        obj = data;
        that.http.get("/update?name="+data.image+"&link="+data.link).subscribe(data => {
          console.log(data);
        });
      } else {       
        obj.isPublish = false;
      }
    })
  }

  publishAll(){
    let that = this;
    this.appData.forEach(function(obj, index){  
      obj.isPublish = true;
      setTimeout(function(y) {    
        that.http.get("/update?name="+obj.image).subscribe(data => {
          console.log(data);
        });
      }, index*2000, index);
    });
  }

  getActiveApp(){
    this.appData.forEach(function(o){
     o.isPublish==true;
    })

    return this.appData;
  }
}



